.search-form {
  margin-bottom: 20px;
}

.search-form input[type="text"] {
  width: 300px;
  padding: 5px;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  padding: 5px 10px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin-top: 40px;
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  overflow: hidden;
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  padding: 10px;
}

.info-item {
  margin: 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #2196f3;
  border: none;
  margin-bottom: 20px;
  padding: 10px 20px;
  display: none;
}

